.app {
  text-align: center;
}

.app-logo {
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-name: slidein;
          animation-name: slidein;
  height: 80px;
}

.app-header {
  background-color: #222;
  height: 240px;
  padding: 20px;
  color: white;
}

.app-title {
  font-size: 3.5em;
}

.app-content {
  font-size: large;
  text-align: center;
  padding-top: 10px;
}

@-webkit-keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%; 
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}

@keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%; 
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}



